body {
  margin: 0;
  padding: 0;
  background: #000;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100dvh;
  height: 100dvh;
  overflow: hidden;
}

.content {
  &__inner {
    canvas {
      display: block;
      background: #111;
      max-height: 100dvh;
      max-width: 100vw;
    }
  }
}
